import React, { useState, useEffect, useMemo } from "react";
import { Table, Card, Container, Row, Col, Form, Pagination } from "react-bootstrap";
import { FaDownload, FaSort } from "react-icons/fa";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { walletsREports } from "../../../api/login/Login";
import "./Userlist.css";
import Loader from "../../../common/loader/Loader";

function Earningreport() {
  const getCurrentDate = () => {
    const today = new Date();
    return today.toISOString().substr(0, 10);
  };

  const [earningsData, setEarningsData] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [sortConfig, setSortConfig] = useState({ key: null, direction: "ascending" });
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);

  const initialFilterState = {
    user_id: window.localStorage.getItem("userToken"),
    count: 10,
    page: 0,
    min_amt: 0,
    max_amt: 0,
    end_date: getCurrentDate(),
    start_date: getCurrentDate(),
    type: "",
    trans_type: "",
    order_id: "",
    txn_id: "",
    sortType: "",
    sortKey: "",
  };

  const [filterValues, setFilterValues] = useState(initialFilterState);
  const [appliedFilters, setAppliedFilters] = useState(initialFilterState);

  useEffect(() => {
    fetchEarningsData();
  }, [appliedFilters, currentPage]);

  const fetchEarningsData = async () => {
    try {
      setLoading(true);
      const response = await walletsREports({ ...appliedFilters, page: currentPage - 1 });

      if (Array.isArray(response.data.data.wallet)) {
        setEarningsData(response.data.data.wallet);
        setTotalCount(response.data.data.total);
      } else {
        console.error("API did not return array:", response.data);
        setEarningsData([]);
        setTotalCount(0);
      }
    } catch (error) {
      console.error("Error fetching earnings data:", error);
      setEarningsData([]);
      setTotalCount(0);
    } finally {
      setLoading(false);
    }
  };

  const handleFilterChange = (event) => {
    const { name, value } = event.target;
    setFilterValues((prev) => ({
      ...prev,
      [name]: typeof value === "string" ? value.trim() : value,
    }));
  };

  const handleSearch = () => {
    setAppliedFilters(filterValues);
    setCurrentPage(1);
  };

  const handleReset = () => {
    const resetFilter = {
      ...initialFilterState,
      start_date: "",
      end_date: "",
    };
    setFilterValues(resetFilter);
    setAppliedFilters(resetFilter);
    setCurrentPage(1);
  };

  const handleSort = (key) => {
    let direction = "ascending";
    if (sortConfig.key === key && sortConfig.direction === "ascending") {
      direction = "descending";
    }
    setSortConfig({ key, direction });
    setAppliedFilters((prev) => ({
      ...prev,
      sortType: direction === "ascending" ? 1 : -1,
      sortKey: key,
    }));
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const sortedData = useMemo(() => {
    let sortableItems = [...earningsData];
    if (sortConfig.key) {
      sortableItems.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key])
          return sortConfig.direction === "ascending" ? -1 : 1;
        if (a[sortConfig.key] > b[sortConfig.key])
          return sortConfig.direction === "ascending" ? 1 : -1;
        return 0;
      });
    }
    return sortableItems;
  }, [earningsData, sortConfig]);

  const totalPages = Math.ceil(totalCount / appliedFilters.count);
  let startPage = Math.max(1, currentPage - 2);
  let endPage = Math.min(totalPages, currentPage + 2);

  const getPaginationRange = () => {
    if (currentPage <= 3) {
      endPage = Math.min(5, totalPages);
    } else if (currentPage >= totalPages - 2) {
      startPage = Math.max(1, totalPages - 4);
    }
    return Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i);
  };

  const paginationRange = getPaginationRange();

  return (
    <>
      {loading && <Loader />}

      <Container fluid className="px-4 PageHeading"
        style={{ backgroundColor: "var(--bs-primary1)", color: "var(--bs-primary2)" }}>

        <h1 className="mt-2 mb-4" style={{ fontWeight: 700 }}>
          Earnings Report
        </h1>

        <Card className="mb-4 er-card"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}>
          <Card.Header className="er-card-header"
            style={{
              backgroundColor: "var(--bs-primary3)",
              color: "var(--bs-primary2)",
              fontWeight: 600,
              borderRadius: "12px 12px 0 0",
              borderBottom: "1px solid var(--bs-primary2)",
            }}>
            Filter
          </Card.Header>

          <Card.Body className="er-card-body">
            <Form>
              <Row className="mb-3">

                <Col md={4}>
                  <Form.Group controlId="startDate">
                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Start Date</Form.Label>
                    <Form.Control type="date" name="start_date" max={getCurrentDate()}
                      value={filterValues.start_date}
                      onChange={handleFilterChange}
                      style={{
                        backgroundColor: "var(--bs-primary1)",
                        color: "var(--bs-primary2)",
                        border: "1px solid var(--bs-primary2)",
                      }} />
                  </Form.Group>
                </Col>

                <Col md={4}>
                  <Form.Group controlId="endDate">
                    <Form.Label style={{ color: "var(--bs-primary2)" }}>End Date</Form.Label>
                    <Form.Control type="date" name="end_date" max={getCurrentDate()}
                      value={filterValues.end_date}
                      onChange={handleFilterChange}
                      style={{
                        backgroundColor: "var(--bs-primary1)",
                        color: "var(--bs-primary2)",
                        border: "1px solid var(--bs-primary2)",
                      }} />
                  </Form.Group>
                </Col>

                <Col md={4}>
                  <Form.Group controlId="order_id">
                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Order ID</Form.Label>
                    <Form.Control type="text" name="order_id"
                      value={filterValues.order_id}
                      onChange={handleFilterChange}
                      style={{
                        backgroundColor: "var(--bs-primary1)",
                        color: "var(--bs-primary2)",
                        border: "1px solid var(--bs-primary2)",
                      }} />
                  </Form.Group>
                </Col>
              </Row>

              <Row className="mb-3">
                <Col md={4}>
                  <Form.Group controlId="txn_id">
                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Txn ID</Form.Label>
                    <Form.Control type="text" name="txn_id"
                      value={filterValues.txn_id}
                      onChange={handleFilterChange}
                      style={{
                        backgroundColor: "var(--bs-primary1)",
                        color: "var(--bs-primary2)",
                        border: "1px solid var(--bs-primary2)",
                      }} />
                  </Form.Group>
                </Col>

                <Row className="mt-3">
                  <Col sm={12} className="d-flex align-items-end gap-2">
                    <button type="button" className="btn"
                      style={{
                        backgroundColor: "#6C757D",
                        color: "#fff",
                        // border: "1px solid var(--bs-primary2)",
                        borderRadius: "10px",
                        fontWeight: 600,
                        padding: "6px 14px",
                      }} onClick={handleSearch}>Search</button>

                    <button type="button" className="btn"
                      style={{
                        backgroundColor: "#F8F9FA",
                        color: "var(--bs-primary2)",
                        // border: "1px solid var(--bs-primary2)",
                        borderRadius: "10px",
                        fontWeight: 600,
                        padding: "6px 14px",
                      }} onClick={handleReset}>Reset</button>
                  </Col>
                </Row>
              </Row>
            </Form>
          </Card.Body>
        </Card>

        <Card className="mb-4 er-card"
          style={{
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
            borderRadius: "12px",
          }}>
          <Card.Header className="er-card-header"
            style={{
              backgroundColor: "#fff",
              color: "var(--bs-primary2)",
              fontWeight: 600,
              borderBottom: "1px solid var(--bs-primary2)",
            }}>
            <Row className="align-items-center justify-content-end w-100">
              <Col lg={12} className="text-end">
                <ReactHTMLTableToExcel
                  id="export-excel-button"
                  className="btn er-download-btn"
                  table="table-to-xls"
                  filename="earnings_report"
                  sheet="earnings_report"
                  buttonText="Download Excel"
                />

              </Col>
            </Row>
          </Card.Header>

          <Card.Body className="er-card-body">
            <Table responsive striped bordered hover id="table-to-xls" className="er-table"
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                border: "1px solid var(--bs-primary2)",
                whiteSpace: "nowrap",
              }}>

              <thead style={{
                backgroundColor: "var(--bs-primary3)",
                color: "var(--bs-primary2)",
                fontWeight: 600,
                textAlign: "center",
              }}>
                <tr style={{ backgroundColor: "var(--bs-primary3)" }}>
                  <th className="text-center er-th" style={{ whiteSpace: "nowrap" }} onClick={() => handleSort("_id")}>
                    S.No <FaSort />
                  </th>
                  <th className="text-center er-th" onClick={() => handleSort("createdAt")}>Date <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("order_id")}>Order ID <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("txn_id")}>Transaction ID <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("amount")}>Amount <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("trans_type")}>Transaction Type <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("approve")}>Approved <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("type")}>Type <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("message")}>Message <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("o_bal")}>Opening Balance <FaSort /></th>
                  <th className="text-center er-th" onClick={() => handleSort("c_bal")}>Closing Balance <FaSort /></th>
                </tr>
              </thead>

              <tbody>
                {sortedData.length > 0 ? (
                  sortedData.map((item, index) => (
                    <tr key={index} className="er-tr"
                      style={{ backgroundColor: "var(--bs-primary1)", color: "var(--bs-primary2)" }}>

                      <td className="text-center">{index + 1}</td>
                      <td className="text-center">{item.createdAt}</td>
                      <td className="text-center">{item.order_id}</td>
                      <td className="text-center">{item.txn_id}</td>
                      <td className="text-center">{item.amount.toFixed(2)}</td>
                      <td className="text-center">{item.trans_type}</td>
                      <td className="text-center">{item.approve ? "Yes" : "No"}</td>
                      <td className="text-center">{item.type}</td>
                      <td className="text-center">{item.message}</td>
                      <td className="text-center">{item.o_bal.toFixed(2)}</td>
                      <td className="text-center">{item.c_bal.toFixed(2)}</td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan="12" className="text-center">No data found</td>
                  </tr>
                )}
              </tbody>
            </Table>

            <div className="d-flex justify-content-between align-items-center mt-3"
              style={{
                backgroundColor: "var(--bs-primary1)",
                color: "var(--bs-primary2)",
                borderTop: "1px solid var(--bs-primary2)",
                padding: "10px",
              }}>
              <div>Total {totalCount} entries</div>

              <Pagination>
                <Pagination.First onClick={() => handlePageChange(1)} disabled={currentPage === 1} />
                <Pagination.Prev onClick={() => handlePageChange(currentPage - 1)} disabled={currentPage === 1} />

                {startPage > 1 && <Pagination.Ellipsis />}

                {paginationRange.map((page) => (
                  <Pagination.Item key={page} active={page === currentPage} onClick={() => handlePageChange(page)}>
                    {page}
                  </Pagination.Item>
                ))}

                {endPage < totalPages && <Pagination.Ellipsis />}

                <Pagination.Next onClick={() => handlePageChange(currentPage + 1)} disabled={currentPage === totalPages} />
                <Pagination.Last onClick={() => handlePageChange(totalPages)} disabled={currentPage === totalPages} />
              </Pagination>
            </div>
          </Card.Body>
        </Card>
      </Container>
    </>
  );
}

export default Earningreport;
